import React, { useEffect, useState } from 'react';
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import { IMAGE_URL } from '../../axios';
import axios from '../../axios';
import './Carousel.css';
import { randomBytes } from 'crypto';

const Carousel = (props) => {

    const [banners, setBanners] = useState([]);

    useEffect(() => {
        axios.get('getBannerBusiness')
            .then(response => { setBanners(response.data.posts) })
            .catch(err => console.log(err));
    }, []);


    const settings = {
        adaptiveHeight: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        autoplay: true,
        fade: true,
        arrows: false,
        autoplaySpeed: 3000
    };

    return (
        <div>
            <Slider {...settings}>

                {
                    banners.map((banner, index) => {
                        return (
                            <div className="image" key={'carousel-' + new Date().getTime()}>
                                <img src={IMAGE_URL + (banner.userImage && banner.userImage !== '' ? banner.userImage : banner.banner.banner)} style={{ height: '200px', width: '100%' }} alt="" />
                                {
                                    banner.userImage && banner.userImage !== '' ? null :
                                        <div className="mycontent" style={{ width: '80%' }}>
                                            <h2 style={{ color: banner.banner ? banner.banner.textColor : 'white', overflowWrap: 'break-word', wordWrap: 'break-word', hyphens: 'auto' }}>{banner.description}</h2>
                                            <h5 style={{ color: banner.banner ? banner.banner.textColor : 'white', overflowWrap: 'break-word', wordWrap: 'break-word', hyphens: 'auto' }}>{banner.city + " | " + banner.country + " | " + banner.mobile}</h5>
                                        </div>
                                }

                            </div>
                        )
                    })
                }

            </Slider>
        </div>
    )
}

export default Carousel;