import React, { useEffect, useState } from 'react';
import MyNav from '../../components/Nav/Nav';
import Post from '../../components/Post/Post';
import CreatePost from '../../components/CreatePost/CreatePost';
import { connect } from 'react-redux';
import { fetchPosts } from '../../store/actions/index';
import Poster from '../../components/Poster/Poster';
import axios from '../../axios';

const SinglePost = (props) => {

    const [post, setPost] = useState(null);

    useEffect(() => {
        const postId = props.match.params.id
        axios.post('getSinglePost', { postId })
            .then(response => {
                if (response.data === 'Link expired') {

                }
                else {
                    setPost(response.data)
                }
            })
            .catch(err => console.log(err.data))
        console.log(props.match.params)
    }, [])

    return (
        <div style={{ backgroundColor: '#f5f5f5' }}>
            <MyNav location={props.location} />
            <br /><br />
            <div className='content'>
                <div className="row">

                    <div className="col s3 hide-on-med-and-down">

                    </div>

                    <div className="col s12 l6 xl6">
                        {
                            post ? <Post post={post} /> : null
                        }
                    </div>

                    <div className="col s3 hide-on-med-and-down">

                    </div>

                </div>
            </div>
        </div>
    )
}


const mapStateToProps = (state) => {
    return {
        posts: state.posts.posts
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        fetchPosts: () => dispatch(fetchPosts())
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(SinglePost);