import React, { useState, useEffect } from 'react';
import { IMAGE_URL } from '../../axios';
import M from "materialize-css";
import axios from '../../axios';
import './Post.css';
import { connect } from 'react-redux';
import Linkify from 'react-linkify';
import Avatar from 'react-avatar';
import { Redirect } from 'react-router-dom';

import {
    FacebookShareButton,
    LinkedinShareButton,
    TwitterShareButton,
    TelegramShareButton,
    WhatsappShareButton,
    EmailShareButton,
    FacebookIcon,
    TwitterIcon,
    LinkedinIcon,
    TelegramIcon,
    WhatsappIcon,
    EmailIcon,
} from 'react-share';
import { SITE_BASE_URL } from '../../axios';

const Post = (props) => {

    const profilePic = (props.post.user.profilePic && props.post.user.profilePic !== "") ? IMAGE_URL + props.post.user.profilePic : null

    const [isLiked, setLiked] = useState(props.post.post.likedByMe);
    const [isShareVisible, setShareVisible] = useState(false);
    const [likeRedirect, setLikeRedirect] = useState(false);

    const likeIcon = isLiked ? 'thumb_up' : 'thumb_up';
    const likeIconColor = isLiked ? 'black' : 'white';

    const shareUrl = `${SITE_BASE_URL}post/${props.post.post._id}`;
    const title = 'Bongkonect';

    const currDate = new Date().getTime();
    const postDate = new Date(props.post.post.createdOn).getTime();
    var msec = currDate - postDate;
    var mins = Math.abs(Math.round(msec / 60000));
    var hrs = Math.floor(mins / 60);

    let timeString = "";
    if (mins <= 60)
        timeString = mins + " minutes ago"
    else
        timeString = hrs + " hours ago"

    useEffect(() => {
        M.AutoInit();
    }, []);

    const deletePost = () => {
        const postId = props.post.post._id;
        axios.post('deletePost', { postId })
            .then(response => props.fetchPosts())
            .catch(error => console.log(error))
    }

    const onLikeClick = () => {
        if (props.isAuthenticated) {
            setLikeRedirect(false);
            if (isLiked && props.post.post.likeCount > 0) {
                setLiked(false)
                props.post.post.likeCount = props.post.post.likeCount - 1
                axios.post('unlikePost', { postId: props.post.post._id })
                    .then(response => console.log(response.data))
                    .catch(error => console.log(error.data))
            }

            else {
                setLiked(true)
                props.post.post.likeCount = props.post.post.likeCount + 1
                axios.post('likePost', { postId: props.post.post._id })
                    .then(response => console.log(response.data))
                    .catch(error => console.log(error.data))
            }
        }
        else {
            setLikeRedirect(true)
        }

    }

    const onShareClick = () => {
        if (isShareVisible) {
            setShareVisible(false)
        }

        else {
            setShareVisible(true)
        }
    }

    return (
        <div>
            {likeRedirect ? <Redirect to='/login' /> : null}
            <div className="card" style={{ width: '100%' }}>
                <div className="card-content">
                    <div className="row">
                        <Avatar name={props.post.user.firstName + " " + props.post.user.lastName} round src={profilePic} size='50px' style={{ display: 'inline-block', verticalAlign: 'middle' }} />
                        {
                            props.userId === props.post.post.userId ? <i className="material-icons right" onClick={(e) => deletePost(e)} style={{ cursor: 'pointer', color: 'black' }}>delete_forever</i> : null
                        }

                        <div style={{ display: 'inline-block', verticalAlign: 'middle', marginLeft: '20px' }}>
                            <h6 style={{ fontWeight: 'bold' }}>{props.post.user.firstName + " " + props.post.user.lastName}</h6>
                            <strong style={{ fontSize: '10px', color: 'grey' }}>{props.post.user.city + " | " + props.post.user.country}</strong>
                            <br /><strong style={{ fontSize: '10px', color: 'grey' }}>{timeString}</strong>
                        </div>
                    </div>

                    <div className="row">
                        <Linkify>
                            <p style={{ overflowWrap: 'break-word', wordWrap: 'break-word', hyphens: 'auto' }}>
                                {props.post.post.description}
                            </p>
                        </Linkify>
                    </div>

                    <div className="">
                        {

                            props.post.post.photos.map((photo, index) => {

                                let image = null;

                                if (props.post.post.photos.length === 1) {
                                    image = <div className="row" key={index}>
                                        <div className="col s12">
                                            <div className="card-image" >
                                                <img className="materialboxed responsive-img" src={IMAGE_URL + photo} alt="" />
                                            </div>
                                        </div>
                                    </div>
                                }

                                if (props.post.post.photos.length === 2) {

                                    if (index === 1) {
                                        image =
                                            <div className="row" key={index}>
                                                <div className="col s6">
                                                    <div className="card-image" >
                                                        <img className="materialboxed responsive-img" src={IMAGE_URL + props.post.post.photos[index - 1]} alt="" />
                                                    </div>
                                                </div>
                                                <div className="col s6">
                                                    <div className="card-image" >
                                                        <img className="materialboxed responsive-img" src={IMAGE_URL + props.post.post.photos[index]} alt="" />
                                                    </div>
                                                </div>
                                            </div>
                                    }
                                }

                                if (props.post.post.photos.length === 3) {

                                    if (index === 2) {
                                        image = <div>
                                            <div className="row" key={index}>
                                                <div className="col s12">
                                                    <div className="card-image" >
                                                        <img className="materialboxed responsive-img" src={IMAGE_URL + props.post.post.photos[index - 2]} alt="" />
                                                    </div>
                                                </div>
                                            </div>
                                            <div className="row">
                                                <div className="col s6">
                                                    <div className="card-image" >
                                                        <img className="materialboxed responsive-img" src={IMAGE_URL + props.post.post.photos[index - 1]} alt="" />
                                                    </div>
                                                </div>
                                                <div className="col s6">
                                                    <div className="card-image" >
                                                        <img className="materialboxed responsive-img" src={IMAGE_URL + props.post.post.photos[index]} alt="" />
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    }
                                }

                                return image

                            })
                        }

                    </div>

                    <div className="card-action">
                        <div style={{ cursor: 'pointer', display: 'inline-block', marginRight: '10px' }} onClick={(e) => onLikeClick(e)}>
                            <button className="btn-floating waves-effect waves-light" disabled><i className="material-icons" style={{ color: likeIconColor }}>{likeIcon}</i></button>
                        </div>

                        <div style={{ cursor: 'pointer', display: 'inline-block', marginLeft: '10px' }} onClick={(e) => onShareClick(e)}>
                            <button className="btn-floating waves-effect waves-light" disabled><i className="material-icons" style={{ color: 'white' }}>share</i></button>
                        </div>
                        <br />
                        <strong style={{}}>{props.post.post.likeCount} likes</strong>


                        <div className={isShareVisible ? null : 'hide'} style={{ textAlign: 'center' }}>
                            <div className="Demo__some-network">
                                <FacebookShareButton
                                    url={shareUrl}
                                    quote={title}
                                    className="Demo__some-network__share-button">
                                    <FacebookIcon
                                        size={32}
                                        round />
                                </FacebookShareButton>
                            </div>

                            <div className="Demo__some-network">
                                <TwitterShareButton
                                    url={shareUrl}
                                    title={title}
                                    className="Demo__some-network__share-button">
                                    <TwitterIcon
                                        size={32}
                                        round />
                                </TwitterShareButton>
                            </div>

                            <div className="Demo__some-network">
                                <TelegramShareButton
                                    url={shareUrl}
                                    title={title}
                                    className="Demo__some-network__share-button">
                                    <TelegramIcon size={32} round />
                                </TelegramShareButton>
                            </div>

                            <div className="Demo__some-network">
                                <WhatsappShareButton
                                    url={shareUrl}
                                    title={title}
                                    separator=":: "
                                    className="Demo__some-network__share-button">
                                    <WhatsappIcon size={32} round />
                                </WhatsappShareButton>
                            </div>

                            <div className="Demo__some-network">
                                <LinkedinShareButton
                                    url={shareUrl}
                                    windowWidth={750}
                                    windowHeight={600}
                                    className="Demo__some-network__share-button">
                                    <LinkedinIcon
                                        size={32}
                                        round />
                                </LinkedinShareButton>
                            </div>

                            <div className="Demo__some-network">
                                <EmailShareButton
                                    url={shareUrl}
                                    subject={title}
                                    body="body"
                                    className="Demo__some-network__share-button">
                                    <EmailIcon
                                        size={32}
                                        round />
                                </EmailShareButton>
                            </div>

                        </div>

                    </div>

                </div>


            </div>
        </div>

    )
}

export const mapStateToProps = (state) => {
    return {
        loading: state.posts.loading,
        userId: state.auth.userId,
        isAuthenticated: state.auth.token !== null
    }
}

export default connect(mapStateToProps)(Post);