import React, { useEffect, useState, useRef } from 'react';
import { IMAGE_URL } from '../../axios';
import { connect } from 'react-redux';
import M from "materialize-css";
import { ToastContainer, toast } from 'react-toastify';
import profileBackground from '../../assets/images/profile-background.png';
import 'react-toastify/dist/ReactToastify.css';
import { uploadPost } from '../../store/actions/create-post';
import { updateProfilePic, updateProfileBackground } from '../../store/actions/index';
import axios from '../../axios';
import Avatar from 'react-avatar';
import { isMobile } from 'react-device-detect';


const CreatePost = (props) => {

    const profilePic = (props.user.profilePic && props.user.profilePic !== "") ? IMAGE_URL + props.user.profilePic : null
    const profileBackgroundPic = (props.user.profileBackground && props.user.profileBackground !== "") ? IMAGE_URL + props.user.profileBackground : profileBackground

    const [loading, setLoading] = useState(false);
    const [description, setDescription] = useState("");
    const [profileBackgrounds, setProfileBackgrounds] = useState([]);
    const [photos, setPhotos] = useState(null);
    const [photoUrls, setPhotosUrl] = useState([]);
    const [buttonDisabled, setButtonDisabled] = useState(true);

    useEffect(() => {
        M.AutoInit();

        axios.get('getProfileBackgrounds')
            .then(response => setProfileBackgrounds(response.data.profileBackgrounds))
            .catch(err => console.log(err.data))
    }, []);

    const profilePicChooser = useRef();

    const onFileChanged = (e) => {
        props.updateProfilePic(e.target.files[0]);
    }

    const onDescriptionChange = (value) => {

        setDescription(value);
        if (value.length > 0)
            setButtonDisabled(false)
        else
            setButtonDisabled(true)

    }

    const onFilesChanged = (event) => {

        var files = event.target.files
        if (maxSelectFile(event) && checkMimeType(event) && checkFileSize(event)) {
            // if return true allow to setState
            setPhotos(files);
            setButtonDisabled(false)

            let photos = [];
            for (let x = 0; x < files.length; x++) {
                photos.push(URL.createObjectURL(files[x]))
            }
            setPhotosUrl(photos);
        }
    }

    const submitForm = (event) => {
        event.preventDefault();
        uploadPost(photos, description);
        setDescription("");
        setPhotos([]);
        setPhotosUrl([]);
    }

    const uploadPost = (photos, description) => {

        setLoading(true);
        const data = new FormData();
        if (photos) {
            for (var x = 0; x < photos.length; x++) {
                data.append('photos', photos[x])
            }
        }
        data.append('description', description);
        axios.post("createPost", data)
            .then(res => {
                setLoading(false);
                props.fetchPosts();
            })
            .catch(err => { // then print response status

            })
    }

    const checkMimeType = (event) => {
        //getting file object
        let files = event.target.files
        //define message container
        let err = []
        // list allow mime type
        const types = ['image/png', 'image/jpeg', 'image/gif']
        // loop access array
        for (let x = 0; x < files.length; x++) {
            // compare file type find doesn't matach
            if (types.every(type => files[x].type !== type)) {
                // create error message and assign to container   
                err[x] = files[x].type + ' is not a supported format\n';
            }
        };
        for (let z = 0; z < err.length; z++) {// if message not same old that mean has error 
            // discard selected file
            toast.error(err[z])
            event.target.value = null
        }
        return true;
    }

    const maxSelectFile = (event) => {
        let files = event.target.files
        if (files.length > 3) {
            const msg = 'Only 3 images can be uploaded at a time'
            event.target.value = null
            toast.warn(msg)
            return false;
        }
        return true;
    }

    const checkFileSize = (event) => {
        let files = event.target.files
        let size = 2000000
        let err = [];
        for (let x = 0; x < files.length; x++) {
            if (files[x].size > size) {
                err[x] = files[x].type + 'is too large, please pick a smaller file\n';
            }
        };
        for (let z = 0; z < err.length; z++) {// if message not same old that mean has error 
            // discard selected file
            toast.error(err[z])
            event.target.value = null
        }
        return true;
    }

    return (
        <div>
            {
                props.error ? toast.error(props.error) : null
            }


            <div id="profileBackgroundModal" className="modal">
                <div className="modal-content">
                    <div className="row" style={{ backgroundColor: 'white' }}>

                        {
                            profileBackgrounds.map((photo) => {
                                return (
                                    <div className="col modal-close" key={photo._id} style={{ marginBottom: '10px', cursor: 'pointer' }} onClick={() => props.updateProfileBackground(photo.profileBackground)}>
                                        <img src={IMAGE_URL + photo.profileBackground} height="100" width="100" alt="" />
                                    </div>
                                )
                            })
                        }

                    </div>
                </div>
            </div>

            <div className="card" style={{ width: '100%' }}>
                <div className="card-image responsive-img">
                    <a className="modal-trigger" href="#profileBackgroundModal"><img src={profileBackgroundPic} alt="" /></a>
                </div>

                <div className="card-content">
                    <div className="row">

                        <div className="col s3">
                            {
                                isMobile ? <Avatar name={props.user.firstName + " " + props.user.lastName} round src={profilePic} size='80px' style={{ zIndex: '5', position: 'absolute', marginTop: '-60px', cursor: 'pointer' }} onClick={(e) => profilePicChooser.current.click()} /> :
                                    <Avatar name={props.user.firstName + " " + props.user.lastName} round src={profilePic} size='150px' style={{ zIndex: '5', position: 'absolute', marginTop: '-100px', cursor: 'pointer' }} onClick={(e) => profilePicChooser.current.click()} />
                            }

                            <input type='file' disabled={props.userLoading} style={{ display: 'none' }} ref={profilePicChooser} onChange={(e) => onFileChanged(e)} />
                        </div>
                        <div className="col s9">
                            <div className="left" style={{ marginTop: '0px' }}>
                                <span style={{ fontWeight: '600', fontSize: '1.5em' }}>{props.user.lastName + " " + props.user.firstName}</span>
                                <br />
                                <span style={{ fontWeight: '600', fontSize: '1em', color: 'grey' }}>{props.user.about}</span>

                                <br />
                                <span style={{ fontWeight: '600', fontSize: '1em', color: 'grey' }}>{props.user.city + " | " + props.user.country}</span>
                            </div>
                        </div>

                    </div>
                    <br />
                    <form encType='multipart/form-data' onSubmit={(e) => submitForm(e)}>

                        <div className="row">
                            <div className="col s8 m10 l10 xl10">
                                <textarea id="textarea2" className="materialize-textarea" onChange={(e) => onDescriptionChange(e.target.value)} value={description} data-length="50" maxLength="300" placeholder="Write something here..." rows="4" style={{ width: '100%' }}></textarea>
                                <small className="right">{description.length}/300</small>
                            </div>
                            <div className="col s4 m2 l2 xl2">

                                <div className="input-field" style={{ display: 'inline-block', cursor: 'pointer' }}>
                                    <button style={{ display: 'inline-block', textAlign: 'center', backgroundColor: 'white', border: '0px' }} disabled={buttonDisabled || props.loading} type="submit" className="waves-effect waves-light "><i className="material-icons" style={{ display: 'inline-block', verticalAlign: 'middle', fontSize: '2.2em' }}>mode_comment</i></button>
                                </div>

                                <div className="file-field input-field" style={{ display: 'inline-block' }}>
                                    <i className="material-icons" style={{ display: 'inline-block', verticalAlign: 'middle', fontSize: '2.2em' }}>add_a_photo</i>
                                    <input type="file" multiple onChange={(e) => onFilesChanged(e)} />
                                </div>
                            </div>

                        </div>

                        <div className="row" style={{ marginBottom: '0px', marginTop: '10px' }}>
                            {
                                photoUrls.map((photo, index) => {
                                    let col = "col"
                                    switch (photoUrls.length) {
                                        case 3: col = "col s4"
                                            break;
                                        case 2: col = "col s6"
                                            break;
                                        case 1: col = "col s12"
                                            break;
                                        default: col = "col"
                                    }
                                    return (
                                        <div className={col}>
                                            <img className="responsive-img" height='200' src={photo} alt="" key={index} />
                                        </div>
                                    )
                                })
                            }
                        </div>

                        <ToastContainer style={{ marginBottom: '0px', marginTop: '10px' }} />
                        {
                            loading ? <div>
                                <span>Uploading...</span>
                                <div className="progress">
                                    <div className="indeterminate"></div>
                                </div>
                            </div> : null
                        }
                    </form>
                </div>
            </div>
        </div >
    )
}

const mapStateToProps = (state) => {
    return {
        user: state.auth.user,
        userLoading: state.auth.loading,

        post: state.post.post,
        loading: state.post.loading,
        error: state.post.error
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        uploadPost: (photos, description) => dispatch(uploadPost(photos, description)),
        updateProfilePic: (profilePic) => dispatch(updateProfilePic(profilePic)),
        updateProfileBackground: (profileBackground) => dispatch(updateProfileBackground(profileBackground))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(CreatePost);